@import '../../DesignSystem';

.sorter {
  background: #f5f7fb;
  height: calc(100vh -  #{$NAVBAR_HEIGHT});
  overflow: hidden;
  display: flex;

  .main-wrapper {
    width: 80%;
    height: 100%;
    overflow-y: scroll;
    padding: 2rem;
    // temporary to be refactored
  }

  .sidebar-wrapper {
    min-width: 350px;
    background: white;
    width: 20%;
    height: 100%;
    background-color: white;
    overflow-y: scroll;
  }

}
