@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: 'Rubik', 'Montserrat', sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.toast {
  position: absolute;
  bottom: 0;
  z-index: 100;
  right: 0;
  margin: 2rem;
}


.navbar{height:85px;width:100%;background:#fff;box-shadow:0px 5px 40px rgba(0,0,0,.05);font-family:Montserrat}.nav-brand{margin-left:2rem}.nav-logo{height:30px}.nav-link{font-weight:500;font-size:16px;color:#212529 !important;margin-right:2rem}.nav-link:hover{color:#09f !important}.season-selector{margin-right:2rem}.dropdown-menu{height:400px;overflow:auto}
.option-bar-container .dropdown-view{margin:2rem}.option-bar-container .dropdown-filter{margin:2rem;left:-5vw}.option-bar-container .dropdown-label-view{margin-right:1vw;margin-top:.5rem;font-family:Montserrat;font-style:normal;font-weight:600;font-size:20px;line-height:24px}.option-bar-container .dropdown-label-filter{margin-right:1rem;margin-top:.5rem;font-family:Montserrat;font-style:normal;font-weight:600;font-size:20px;line-height:24px}.option-bar-container #selector{width:auto;background:#fff;border-radius:10px;border:none;font-family:Montserrat;font-weight:300;font-size:20px;color:#000;box-shadow:7px 7px 14px #eaeaea}.option-bar-container .selector-menu{margin-top:10px;border-radius:10px;border:none;font-family:Montserrat;font-size:18px;box-shadow:7px 7px 14px #bfbfbf}.option-bar-container .add-button{margin:2rem;min-width:200px;max-height:50px;background:#4b4b92;border-radius:10px;border:none;font-family:Montserrat;font-style:normal;font-weight:300;font-size:20px;margin-left:3vw}.program-gallery{display:flex;flex-wrap:wrap}.program-side-info_container{width:550px;height:auto;background-color:#fff}
.instructor_table{padding:0 5rem}.instructor_table table{text-align:center;border-spacing:0 .5rem;border-collapse:separate}.instructor_table table .instructor_row{border-radius:10px;background:#fff}.instructor_table table .instructor_row:hover{background:#f5f5f5;transition:background .2s;cursor:pointer}.instructor_table table .instructor_row td:first-child{border-radius:10px 0 0 10px}.instructor_table table .instructor_row td:last-child{border-radius:0 10px 10px 0}.badge{margin:.2rem;font-weight:400;padding:.5rem 1.2rem;font-size:1rem}.grouped-info_container{display:flex;margin:1rem}.info{background:#f5f7fb;padding:1rem;margin:0 .5rem;border-radius:10px}.info-transparent{padding:.5rem;margin:0 .5rem;border-radius:10px}h6{color:#676c72}
.sorter{background:#f5f7fb;height:calc(100vh - 85px);overflow:hidden;display:flex}.sorter .main-wrapper{width:80%;height:100%;overflow-y:scroll;padding:2rem}.sorter .sidebar-wrapper{min-width:350px;background:#fff;width:20%;height:100%;background-color:#fff;overflow-y:scroll}
.sidebar{position:-webkit-sticky;position:sticky;right:0;top:0;padding:1rem 2rem}.sidebar .title{font-weight:bold;font-size:32px;margin-top:2rem;color:#212529;text-align:center}.sidebar .search{margin:2rem 0rem;width:auto !important}.sidebar .search .search-bar{background-color:#f2f5f7;border-radius:10px;border:none}.sidebar .search .search-btn{background-color:#09f;border-radius:0px 10px 10px 0px;border:none;width:3rem}.sidebar .filter-btn{background-color:#fff;border-color:#4b4b92;border-width:2px;color:#4b4b92;border-radius:10px;padding:10px 15px;width:40%;height:40px;margin-right:1.5rem;margin-bottom:1rem;font-size:14px;font-weight:600}.sidebar .filter-btn:hover{background-color:#4b4b92;color:#fff}.sidebar .auto-assign-btn{background-color:#4b4b92;border-radius:10px;padding:10px 15px;width:50%;height:40px;font-size:14px;font-weight:600;margin-bottom:1rem;border:none}.sidebar .auto-assign-btn:hover{background-color:#fff;border:2px solid #4b4b92;color:#4b4b92}
.filter{float:right;top:-2rem;right:-0.5rem;height:100%;min-width:20%}.filter h5{padding-top:1rem}.filter .filter-group{padding-left:1rem}.filter .filter-btns{margin-top:3rem;text-align:center;display:flex;flex-direction:column;justify-content:center;align-items:center}.filter .apply-btn{margin-bottom:2rem;font-size:18px;width:10rem;height:40px;background-color:#09f;border:none;border-radius:10px}.filter .apply-btn:hover{background-color:#fff;color:#09f;border:2px solid #09f}.filter .reset-btn{margin-bottom:3rem;font-size:18px;color:#09f;background-color:#fff;border:none}.filter .reset-btn:hover{background-color:#fff;color:#e82029}
.instructor{background-color:#f2f5f7;margin:0rem 2rem 1rem 2rem;border-radius:10px;height:auto;width:auto;color:#202e47;font-weight:600;font-size:18px;padding:10px 20px}.instructor:hover{background-color:#e7e9eb}.instructor .name{color:#202e47;font-size:18px;font-weight:600;margin-bottom:1rem}.instructor .instructor-lock{float:right;padding:-1rem;z-index:2}.instructor .pref{margin-bottom:1rem}.instructor .tags{display:flex;font-weight:normal;font-size:14px;text-align:center;margin-bottom:.5rem}.instructor .tag{background:#f2c94c;border-radius:30px;width:auto;display:inline-block;padding:3px 10px;margin-right:.5rem}.instructor .edit-icon{float:right;margin-top:-2rem;z-index:2}
.program .program-module{margin:1rem;border-radius:10px !important;border:0 !important}.program .program-header{color:#fff;height:4rem;display:block}.program .program-header:hover{cursor:pointer}.program .caret-down{color:#fff;transform:rotate(90deg);transition:all .3s}.program .caret-right{color:#fff;transition:all .3s}.program .program-name{font-weight:600;font-size:24px;padding-left:1rem;display:inline-block;vertical-align:middle;color:#fff}.program .lock{z-index:5;float:right;padding-top:.5rem}.program .classes-container{background-color:rgba(221,223,226,.8);border:none !important}.program .class{margin:.5rem 1rem}
.class{background-color:#fff;margin:1rem;border-radius:10px;padding-bottom:1rem;width:20rem}.class .header{display:inline-flex}.class .partner-name{color:#202e47;font-weight:bold;font-size:22px;padding:2rem 2rem 0rem}.class .lock{float:right;padding:1rem;z-index:2}.class .class-info{padding-bottom:1rem;margin-left:2rem}.class .spacer{min-height:1px}
.gradient-border:active{transition:transform 300ms ease;transition:all 300ms ease-out}.gradient-border:hover{z-index:1;transform:scale(1.07) perspective(90px) rotateX(0.25deg);cursor:pointer}
